import React from 'react';
import { Col, Row } from 'react-materialize';

const TechToolsItem = (props) => (
    <Row className="tech-tool-item">
        <Col s={2} m={2} className={props.imagepos}>
            <img src={props.image} alt={props.imgtitle} title={props.imgtitle}  className="responsive-img"/>
        </Col>
        <Col className={props.descpos} s={9} m={8}>
            <p>{props.desc}</p>
        </Col>   
    </Row>
);

export default TechToolsItem;


