import React from 'react';
import { Row,Col } from 'react-materialize';
import ProfileCard from '../profilecard/profilecard';
const Banner = () => (
    <Row className="banner">
        <Col className="center cardflow" m={4}>
            <ProfileCard />
        </Col>
    </Row>
    
);

export default Banner;


