import React from 'react';
import { Row } from 'react-materialize';

const About = () =>(
    <Row className="about">
        <div className="center context">
            <h5 className="subtitle">SOMETHING ABOUT ME</h5>
            <p className="align-center">
                I started in computers world at 2010 at midle grade, I learned a lot about coding and programing logic. In 2011 I had my first contact with web since them this as been my focous, during this grade Ive created desktop app and web pages. As more i created the love of code for web as growing. Since then i decided to work with web develoment system and mobile apps using web tech. I love to interact with the comunity the idea of sharing experience is awesome. Subjects beside coding that i like to talk about Astronony, Physics, Amines, Sci-fi.. etc. :D  
            </p>      
        </div>
    </Row>
);

export default About;


